<template>
    <div>
      <el-col :span="24">
        <el-form :inline="true" :model="formInline" class="demo-form-inline">
          <el-form-item label="业务类型">
              <el-select v-model="formInline.value1" placeholder="请选择业务类型" clearable>
                  <el-option label="体检" value="体检"></el-option>
                  <el-option label="照相" value="照相"></el-option>
                </el-select>
          </el-form-item>
          <el-form-item label="日期选择">
            <el-date-picker
                  v-model="formInline.value2"
                  type="daterange"
                  range-separator="至"
                  start-placeholder="开始日期"
                  end-placeholder="结束日期">
                </el-date-picker>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onSubmit">查询</el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="exportExcel">下载统计表</el-button>
          </el-form-item>
        </el-form>
      </el-col>
      <div style="margin: 10px 0;color: red;">注：默认显示本日数据，数据包含体检完成人数、体检未完成人数、中断提交人数等所有体检人数。</div>
      <el-card>
        <el-row><el-col :span="4" :push="20"><el-button @click="modeSwitch">{{modeDeom}}</el-button></el-col></el-row>
        <ve-histogram :settings="chartSettings" :data="chartData" v-if="modeClick"></ve-histogram>
         <el-table
            v-else
            :data="chartData.rows"
            style="width: 100%">
            <el-table-column
              prop="Machine"
              label="设备编号"
              width="180">
            </el-table-column>
            <el-table-column
              prop="Completed"
              label="已完成"
              width="180">
            </el-table-column>
            <el-table-column
              prop="NotCompleted"
              label="未完成">
            </el-table-column>
            <el-table-column
              prop="Total"
              label="总数">
            </el-table-column>
          </el-table>
      </el-card>
    </div>
</template>
<script>
    const axios = require("axios");
    import VeHistogram from 'v-charts/lib/histogram'
  	export default {
      data(){
        return{
          chartData:'',
          chartSettings: {
            labelMap: {'Completed': '已完成', 'NotCompleted': '未完成'}
          },
          formInline:{
            value1:'',
            value2:''
          },
          modeDeom:'表格方式',
          modeClick:true
        }
      },
      methods:{
        onSubmit(){
          console.log('查询')
        },
        modeSwitch(){
          if(this.modeClick){
            this.modeClick = false
            this.modeDeom = '图表方式'
          }else{
            this.modeClick = true
            this.modeDeom = '表格方式'
          }
        },
        exportExcel() {
          require.ensure([], () => {
            const { export_json_to_excel } = require('../../excel/Export2Excel');
            const tHeader = ['设备编号', '审核通过', '审核失败','总数'];
            // 上面设置Excel的表格第一行的标题
            const filterVal = ['Machine', 'Completed', 'NotCompleted','Total'];
            // 上面的index、nickName、name是tableData里对象的属性
            const list = this.chartData.rows;  //把data里的tableData存到list
            const data = this.formatJson(filterVal, list);
            export_json_to_excel(tHeader, data, '体检人数统计');
          })
        },
        formatJson(filterVal, jsonData) {
          return jsonData.map(v => filterVal.map(j => v[j]))
        }
      },
      created () {
          this.chartData = {
            columns: ["Machine", "Completed","NotCompleted"],
            rows: [
              { Machine: 'tjj001', Completed: 1232, NotCompleted:123,Total:1355},
              { Machine: 'tjj002', Completed: 1223 , NotCompleted:232,Total:1335},
              { Machine: 'tjj003', Completed: 2123 , NotCompleted:443,Total:1235},
              { Machine: 'tjj004', Completed: 4123 , NotCompleted:231,Total:1545},
              { Machine: 'tjj005', Completed: 3123 , NotCompleted:223,Total:1657},
              { Machine: 'tjj006', Completed: 1000 , NotCompleted:121,Total:1337},
              { Machine: '合计', Completed:11232, NotCompleted: 8678,Total:17897}
            ]
          }
        },
        components: { VeHistogram }
    }
</script>
<style scoped>

</style>
